import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import { useEffect, useState } from 'react';

const HostLoadingPage = () => {
  const imageUrl = process.env.PUBLIC_URL + '/images/BG_blur.png';

  let navigate = useNavigate();
  const [guestName, setGuestName] = useState('');
  const [hostName, setHostName] = useState('');

//   useEffect(() => {
//     const timer = setTimeout(() => {
//       navigate('/hostNickname');
//     }, 300); // 0.3  

//     return () => clearTimeout(timer); //      
//   }, [navigate]);

  // 
  const set_prepositional_particle = (name)=>{
    //name   (UTF-16) 
    const charCode = name.charCodeAt(name.length - 1);
        
    //       
    const consonantCode = (charCode - 44032) % 28;

    if(consonantCode === 0){
        //0   -> 
        return `${name}`;
    }
    //1   -> 
    return `${name}`;
  }

  return (
    <BackGround>

      <Image bg={imageUrl}>
        <Contents>
          <Text>{set_prepositional_particle(guestName)}</Text>
          <Text></Text>
          <Text>{hostName}</Text>
          <Text> </Text>
        </Contents>      
      </Image>
    </BackGround>
  )
}

export default HostLoadingPage;

const BackGround = styled.div`
  display: flex;
  justify-content: center;
`

const Image = styled.div`
  background-image: url(${props => props.bg});

  display: flex;
  align-items: center;
  justify-content: center;

  background-size: cover;
  background-position: center;
  width: 375px;
  height: 812px;
  flex-shrink: 0;
`

const Contents = styled.div`
flex-direction: column;
align-items: center;

width: 280px;
display: flex;
`
const Text = styled.div`
color: var(--Brown, #64422E);
text-align: center;
font-family: Spoqa Han Sans Neo;
font-size: 20px;
font-style: normal;
font-weight: 700;
line-height: normal;
`